*,body {
    box-sizing: border-box;
    align-content: center;
}
 .App {
     width: 100%;
     justify-content: center; 
     align-items: center;
     margin: 0 auto;
}
 p {
     text-align: center;
     font-weight: 500;
}
 input[type=text],input[type=email], select, textarea {
     width: 100%;
     padding: 12px;
     border: 1px solid #ccc;
     border-radius: 4px;
     box-sizing: border-box;
     margin-top: 6px;
     margin-bottom: 16px;
     resize: vertical;
}
 input[type=submit] {
     background-color: #4CAF50;
     color: white;
     padding: 12px 20px;
     border: none;
     border-radius: 4px;
     cursor: pointer;
}
 input[type=submit]:hover {
     background-color: #45a049;
}
.scrollbar {
    overflow-y: scroll;
}
.container {
     flex: 1;
     justify-content: center;
     align-items: center;
     vertical-align: top;
     height: 100vh;
     width: 100%;
     border-radius: 5px;
     background-color: #eeeeee;
     padding-top: 5px;
}
.headerbar {
    flex: 1;
    justify-content: center;
    align-items: center;
    vertical-align: middle;
    height: 20px;
    width: 100%;
    border-radius: 5px;
    padding-bottom: 5px;
}

.leaflet-container {
    height: 400px;
    width: 100%;
  }